import React, { useState, useEffect } from "react";
/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx, css } from "@emotion/react";
import {
  FormDiv,
  BtnDiv,
  SubmitBtn,
  CancelBtn,
  DropZoneDiv,
} from "../../css/CommunityElement.js";
import { withRouter } from "react-router-dom";
import axios from "axios";
import FileUploadArea from "../../../utils/FileUploadArea.js";
import FileShowArea from "../../../utils/FileShowArea.js";

function UploadForm(props) {
  const [Title, setTitle] = useState("");
  const [Content, setContent] = useState("");
  const [Image, setImage] = useState([]);

  const updateImages = (newImages) => {
    setImage(newImages);
  };

  const submitHandler = (e) => {
    e.preventDefault();
    if (!Title || !Content) {
      return alert("  .");
    }
    const body = {
      auther: props.user._id,
      email: props.user.email,
      title: Title,
      content: Content,
      images: Image,
      category: props.category,
    };

    axios.post("/api/community/postSubmit", body).then((response) => {
      if (response.data.success) {
        alert("  ");
        props.history.push("/community");
      } else {
        alert("  ");
      }
    });
  };

  const SwitchSubCategory = () => {
    switch (props.category) {
      case "":
        return <></>;
      case "":
        return <></>;
      case "":
        return <></>;
      case "":
        return <></>;
      case "":
        return <></>;
    }
  };

  return (
    <>
      <FormDiv onSubmit={submitHandler}>
        <input
          name="title"
          className="title"
          placeholder=" "
          value={Title}
          onChange={(e) => setTitle(e.currentTarget.value)}
        />
        <div className="filterDiv"></div>
        <textarea
          name="content"
          className="content"
          placeholder={
            "            .\n          .\n\n        ,           .\n\n    \n      \n        "
          }
          value={Content}
          onChange={(e) => setContent(e.currentTarget.value)}
        ></textarea>

        <DropZoneDiv>
          <FileUploadArea Images={Image} setImages={setImage} />
          {Image[0] ? (
            <FileShowArea Images={Image} setImages={setImage} />
          ) : null}
        </DropZoneDiv>

        <BtnDiv>
          <CancelBtn
            onClick={() => {
              props.history.push("/community");
            }}
          >
            
          </CancelBtn>
          <button css={SubmitBtn} type="submit">
            
          </button>
        </BtnDiv>
      </FormDiv>
    </>
  );
}

export default withRouter(UploadForm);
